import React, { Component } from 'react';
import ImagesData from 'ImagesData'
import King from '../images/chess-king.png'
import Kyu from '../images/kyu.png'

class Construction extends Component {
  constructor () {
    super();
    this.state = {
      src: ImagesData[0],
    }
    this.handleClick = this.handleClick.bind(this);
    this.index = 0;
  }

  roll(min, max, target) {
    min = Math.ceil(min);
    max = Math.floor(max);
    let roll = Math.floor(Math.random() * (max - min)) + min,
      win = roll === target;
    console.log(win ? 'Won!' : 'Lost.');
    return win;
  }

  handleClick(e) {
    this.setState({
      src: ImagesData[this.index]
    })
    this.index = (this.index +1)%ImagesData.length;
  }

  render() {
    return(
      <div className="ui centered grid fade-in">
        <div className="row">
          <div className="ui medium image">
            <img src={this.roll(0, 100, 69) ? Kyu : this.state.src} alt={King} onClick={this.handleClick}/>
          </div>
        </div>
        <div className="row">
          <h5>coming soon :)</h5>
        </div>
      </div>
    )
  }
}

export default Construction;



