import React, { Component } from 'react';
import Card from 'Card'
import Skill from 'Skill'
import 'Portfolio.css'
import {PortfolioData, SkillsData} from 'PortfolioData'

class Portfolio extends Component {

  constructor () {
    super();
    const generateSkills = skill => {
      return <Skill key={skill.src} url={skill.url} name={skill.name} src={skill.src}/>
    };

    this.languages = SkillsData.languages.map(generateSkills)
    this.frontend = SkillsData.frontend.map(generateSkills)
    this.cards = PortfolioData.map(card => {
      return <Card key={card.url} src={card.src} url={card.url}/>
    })
  }

  render() {
    return(
      <div className="ui portfolio doubled centered grid fade-in">
        <div className="two column row">
          <div className="column">
            <div className="ui centered grid">
              <div className="intro languages row">
                Languages
              </div>
              <div className="row">
                {this.languages}
              </div>
            </div>
          </div>
          <div className="column">
            <div className="ui centered grid">
              <div className="intro libraries row">
                Design / Libraries
              </div>
              <div className="row">
                {this.frontend}
              </div>
            </div>
          </div>
        </div>

        <div className="intro row" style={{'marginTop': '50px'}}>
          Portfolio
        </div>
        <div className="row">
          <div className={`ui ${this.props.mobile ? 'stackable' : 'four'} centered cards`}>
            {this.cards}
          </div>
        </div>
      </div>
    )
  }
}

export default Portfolio;



