import React, { Component } from 'react';

class Button extends Component {
  render() {
    return (
      <a href={this.props.href} target="_blank" rel="noopener noreferrer" className={`ui ${this.props.size} ${this.props.color} circular ${this.props.company} icon button`}>
        <i className={this.props.icon + " icon"}></i>
      </a>
    );
  }
}

export default Button;



