import React, { Component } from 'react';
import KingPNG from '../images/chess-king.png'

class Card extends Component {
  render() {
    return(
      <div className="ui raised link card">
        <a className="ui fluid medium image" onClick={this.props.handleClick} href={this.props.url} target="_blank" rel="noopener noreferrer">
          <img onClick={this.props.handleClick} src={this.props.src} alt={KingPNG}></img>
        </a>
      </div>
    )
  }
}

export default Card;



