import React, { Component } from 'react';
import Button from 'Button'
import ContactData from 'ContactData'
import Typed from 'typed.js'
import 'Home.css'
/*
import splash from '../images/splash.png'
import mask from '../images/mask.png'
const Aquarelle = window.Aquarelle;
*/

const $ = window.$;

class Home extends Component {
  constructor(props) {
    super(props)
      this.message = this.props.mobile ? 'tap to continue' : 'use the arrow keys.'
      this.buttons = ContactData.map(button => {
        return <Button key={button.href} href={button.href} company={button.company} icon={button.icon} color={button.color} size={this.props.mobile ? 'small' : 'large'}/>
      })

    this.titles =
    [
      'Student',
      'Researcher',
      'Designer',
      'Developer.',
    ]

    this.componentDidMount = this.componentDidMount.bind(this);
    this.componentWillUnmount = this.componentWillUnmount.bind(this);
    }


/*  aq() {
    if(!this.props.mobile) {
      new Aquarelle(splash, mask, {
          autoplay: true,
          loop: true,
          duration: 20000
      }).addEventListener('created', function() {
          var canvas = this.getCanvas();
          $('.canvas').append(canvas)
          $('body').css('overflow', 'hidden')
      });
    }
  }
*/

  componentDidMount () {
    function setupPulse() {
      $('.pulse > h5').hide();
      setTimeout(function(){$('.pulse > h5').show();setInterval(function() {$('.pulse').fadeToggle('slow', 'linear').fadeToggle('slow', 'linear');}, 2000);}, 1500);
    }

    //this.aq();
    setupPulse();
    this.typed = new Typed('.name', {
      strings: [this.props.name],
      typeSpeed: 100,
      showCursor: false,
      onComplete: () => {
        new Typed(".title", {
          strings: this.titles,
          typeSpeed: 100,
          backSpeed: 50,
          backDelay: 1250,
          smartBackspace: false,
          showCursor: false,
        });
      }
    });
  }

  componentWillUnmount() {
    //$('canvas').remove();
    $('body').css('overflow','auto')
    this.typed.destroy();
  }


  render() {
    return (
      <div className='ui centered grid container'>
        <div className='heading row'>
          <div className='ui container'>
            <span className='name'></span>
            <span className='title'></span>
          </div>
        </div>
        <div className="buttons row">
          <div className="ui container">
            {this.buttons}
          </div>
        </div>
        <div className='pulse row'>
          <h5 className="fade-in">{this.message}</h5>
        </div>
      </div>
    );
  }
}

export default Home;



