import React, {Component} from 'react';
import Content from 'Content'
import 'App.css'
import ReactGA from  'react-ga'

const $ = window.$;

class App extends Component {

  constructor() {
    super();

    ReactGA.initialize('UA-104135910-1', {
      debug: false,
    });

    this.mobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    this.views = ['home', 'portfolio', 'experience', /*'publications', 'construction'*/]

    this.state = {
      view_index: 0,
      view: this.views[0]
    };

    this.handleChange = this.handleChange.bind(this);
    this.componentDidMount = this.componentDidMount.bind(this);
    this.handleTap = this.handleTap.bind(this);
    this.setupKeys = this.setupKeys.bind(this);
  }

  handleTap(e) {
    let clicked = $(e.target),
      tag = clicked.prop('tagName'),
      classes = clicked.attr("class");

    if(typeof classes === 'undefined') {
      classes = ''
    }

    ReactGA.event({
      category: 'Mobile',
      action: 'tapped',
      label: tag + ": " + classes,
    })

    let blacklist = ['a', 'i', 'svg', 'path']
    if($.inArray(tag.toLowerCase(), blacklist) !== -1){
      return;
    } else {
      this.handleChange((this.state.view_index + 1) % (this.views.length))
    }
  }

  setupKeys () {
    $(document).on('keyup', (e) => {
      let new_index,
        val = e.which;
      if (val === 37 || val === 40 || val === 74) {
        new_index = (this.state.view_index - 1) % (this.views.length);
        if(new_index < 0) {
          new_index = this.views.length -1;
        }
      } else if (val === 38 || val === 39 || val === 76){
        new_index = (this.state.view_index + 1) % (this.views.length);
      } else if (val >= 49 && val <= 52){
        new_index = val - 49;
      } else {
        return
      }

      this.handleChange(new_index)
    })

  }
  componentDidMount () {
    ReactGA.pageview('/');
    ReactGA.pageview(this.state.view);
    this.setupKeys();
  }

  handleChange (new_index) {
    this.setState({
        view_index: new_index,
        view: this.views[new_index]
    });
    ReactGA.pageview(this.state.view);
  }

  render() {
    return(
      <div className='ui centered container fade-in noselect' onClick={this.mobile ? this.handleTap : null}>
        <Content view={this.state.view} mobile={this.mobile}/>
      </div>
    )
  }
}

export default App;



