import React, { Component } from 'react';
import KingPNG from '../images/chess-king.png'

class Entry extends Component {
  render() {
    return(
      <div className="item">
          <div className="ui fluid small image" /*href={this.props.url} target="_blank" rel="noopener noreferrer"*/>
            <img src={this.props.src} alt={KingPNG}/>
          </div>
        <div className="content">
          <a className="header" href={this.props.url} target="_blank" rel="noopener noreferrer">{this.props.title}</a>
          <div className="meta">
            <span>{this.props.setting}</span>
          </div>
          <div className="description">
            <p>{this.props.description}</p>
          </div>
          <div className="extra">
            {this.props.extra}
          </div>
        </div>
      </div>
    )
  }
}

export default Entry;



