import React, { Component } from 'react';
import ReactSVG from 'react-svg'

class Skill extends Component {

  render() {
    return(
      <a key={this.props.src} /*href={this.props.url} target="_blank" rel="noopener noreferrer"*/ data-tooltip={this.props.name} data-inverted="" data-position="top center">
        <ReactSVG path={this.props.src}/>
      </a>
    )
  }
}

export default Skill;



